@import "_BV_variables";

.content {
	background-color: $bright;

	padding: 20px 60px;

	// Bootstrap Customizations
	.row {
		justify-content: flex-start;

		margin-left: 0px; }

	.col {
		flex-grow: 0;

		margin-bottom: 40px; } }

.book-title {
	margin-top: 5px;

	span {
		font-size: 0.9em;
		opacity: 0.7; } }

.book-cover {
	width: 200px; }

.book-rating {
	color: $goodreads-logo-color; }

.stars {
	float: left;
	letter-spacing: -0.07rem; }

.ratings-count {
	float: right; }

.author {
	display: block;
	width: 200px;
	height: 200px;
	padding: 40px;
	border: 1px solid $light-overlay05;
	border-radius: 50%;

	text-align: center;
	overflow: hidden;

	cursor: pointer;

	background-color: $brightest;
	color: $darkest;

	&:hover {
		background-color: $brightest;
		color: $logo-color;
		border-color: $logo-color;

		text-decoration: none; } }
